<template>
  <div>

    <router-link to="/category/table/test">table/test</router-link>
    <cc-table style="height:600px;width:600px;"
      :data="gridData"
      :grid="grid"
      @sortby="sortBy"
      :column="sort.column"
      :type="sort.sort_type"
    >
      <cc-table-col name="isUserBlacklist">
        <template slot-scope="{row}">
          <i v-if="row.isUserBlacklist==0" class="ex--mark">!</i>
        </template>
      </cc-table-col>
      <cc-table-col name="topproxyAccount" :label="'总代'">
        <template slot-scope="{row}">
          <a :href="'/UserList/level_info/'+row.topproxyId">
            <u>{{ row.topproxyAccount }}</u>
          </a>
        </template>
      </cc-table-col>
      <cc-table-col name="userAccount" :label="$t('rp_acc')" sort="account">
        <template slot-scope="{row}">
          <a :href="'/UserList/level_info/'+row.userId">
            <u>{{ row.userAccount }}</u>
          </a>
        </template>
      </cc-table-col>
      <cc-table-col name="nickName" :label="$t('rp_nickname')" fixed sort="nickname"></cc-table-col>
      <cc-table-col name="vipLevel" :label="$t('rp_vip')" v-if="true"></cc-table-col>
      <cc-table-col name="typeText" :label="$t('rp_acc_type')" sort="type"></cc-table-col>
      <cc-table-col name="levelText" :label="$t('rp_level')"></cc-table-col>
      <cc-table-col name="uggId" :label="$t('rp_auth')">
        <template slot-scope="{row}">
          <span
            :title="row.mapName+(row.mapStatus?'':('('+row.mapMsg+')'))"
            :class="[row.mapStatus?'':'status-lock']"
          >{{ row.mapName }}</span>
        </template>
      </cc-table-col>
      <cc-table-col name="statusText" :label="$t('rp_status')" sort="status">
        <template slot-scope="{row}">
          <span
            :title="row.statusText"
            v-bind:class="[row.status!==0?'status-lock':'']"
          >{{ row.statusText }}</span>
        </template>
      </cc-table-col>
      <cc-table-col
        name="topproxyAuditText"
        :label="$t('rp_distributor_check_status')"
        sort="audit"
      >
        <template slot-scope="{row}">
          <span
            @click="checkShowNote(row)"
            :class="{ lowDash: row.note_text}"
          >{{ row.topproxyAuditText }}</span>
        </template>
      </cc-table-col>
      <cc-table-col name="lockBalance" :label="$t('rp_cash_amount')" sort="lock_balance"></cc-table-col>
      <cc-table-col name="cashBalance" :label="$t('rp_available_amount')" sort="available_balance"></cc-table-col>
      <cc-table-col name="createTime" :label="$t('rp_reg_date')" sort="create_time"></cc-table-col>
      <cc-table-col name="loginTime" :label="$t('rp_last_login')" sort="login_time"></cc-table-col>
      <cc-table-col name="userIp" :label="$t('最后登入IP')"></cc-table-col>
      <cc-table-col name="opt" class="option-icon_bg">
        <template slot-scope="{row}">
          <template v-if="row.enable == 0">
            <a :href="'/UserList/user_detail/'+row.userId" :title="$t('ui_user_detailed_info')">
              <i class="detail"></i>
            </a>
            <a @click="editDailog(row)" :title="$t('ui_edit_user')">
              <i class="edit"></i>
            </a>
            <a @click="cardDailog(row)" :title="$t('ui_modi_bankcard')">
              <i class="card"></i>
            </a>
            <a @click="rechargeDailog(row, 0)" class :title="$t('ui_dep')">
              <i class="reserve"></i>
            </a>
            <a @click="rechargeDailog(row, 1)" :title="$t('ui_deduct')">
              <i class="withdraw"></i>
            </a>
            <a @click="cancelBetAmountDailog(row)">
              <i
                class="fa fa-unlock fa-flip-horizontal fa-2x hover"
                style="color: #9254be;"
                aria-hidden="true"
              ></i>
            </a>
          </template>
          <template v-else>
            <a></a>
            <a @click="editDailog(row)" :title="$t('ui_edit_user')">
              <i class="edit"></i>
            </a>
            <a></a>
            <a></a>
            <a></a>
            <a></a>
          </template>
        </template>
      </cc-table-col>
      <cc-table-col name="ui_bonus_group" :label="'奖金组'"></cc-table-col>
      <cc-table-col name="totalDepositAmount" :label="$t('rp_total_user_recharge')"></cc-table-col>
      <cc-table-col name="totalWithdrawalAmount" :label="$t('rp_total_user_withdrawal')"></cc-table-col>
    </cc-table>
  </div>
</template>

<script>
import listData from '@/json/data.json'
console.log('listData', listData)
export default {
  name: 'tables',
  data () {
    return {
      demoRowData: {
        id: '1',
        name: 'Aaron'
      },
      gridData: [],
      sort: {
        column: '',
        sort_type: ''
      }
    }
  },
  created () {
    // this.gridData.push(this.demoRowData)
    this.gridData = listData.data.list
    this.grid = listData.field
  },
  methods: {
    sortBy () {
      console.log('sortBy')
    },
    $t (t) {
      return t
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.tree-layout {
  padding: 10px 0;
  list-style: none;
  /*min-height: 500px;*/
  height: 500px;
  overflow-x: hidden;
  list-style: none;
  text-align: left;
}
</style>



